.team {
  display: flex;
  flex-direction: column;
  background: #dad6d6;
  font-size: 1rem;
  align-items: center;
  justify-content: center;
  height: 53vw;
  padding-bottom: 5vh;
}

.top-title {
  font-size: 2.3rem;
  margin-top: 10px;
}

.top-title::before {
  content: "";
  background: #000;
  display: block;
  height: 8px;
  width: 6vh;
  margin: 50px auto -53px;
  border-radius: 2px;
}

.top-title::after {
  content: "";
  background: #000;
  display: block;
  height: 5px;
  width: 25vh;
  margin: 0.5px auto 10px;
  border-radius: 2px;
}

.team-full {
  display: flex;
  margin: 1.5rem;
  height: 75vh;
}

.team-intro {
  display: table;
  margin: 1.1rem;
  box-shadow: 5px 10px 18px #888888;
  /* background-color: red; */
  background-color: #fff;
}

.team-intro:hover {
  box-shadow: 0 2px 20px #333;
}

.img-team {
  margin-bottom: 20px;
}

.img-all {
  width: 100%;
}

.team-bio {
  margin: 5px 15px;
}

.team-bio > h2 {
  margin-bottom: 15px;
  font-weight: 600;
}

.team-bio > p {
  margin-bottom: 15px;
}

.title {
  color: #8a8888;
}

.team-btn {
  padding: 8px 30px;
  width: 100%;
  border: none;
  outline: none;
  margin-bottom: 10px;
  color: #fff;
  background-color: #000;
  cursor: pointer;
}

@media screen and (max-width: 960px) {
  .top-title {
    margin-top: 22rem;
  }
  .team-full {
    display: block;
  }
  .team-btn {
    display: table;
  }
  .team-intro {
    margin-bottom: 10vh;
    width: 70vw;
  }
  .img-all {
    width: 100%;
  }
}
