.footer {
  display: flex;
  flex-direction: column;
  height: 65vh;
  background-color: #d4d3d3;
  font-size: 1rem;
  /* align-items: center; */
}

.footer-all {
  margin: 9vh 10px 30px 20vh;
}

.footer-up {
  display: flex;
  margin-bottom: 40px;
}

.footer-links {
  display: flex;
  flex-direction: column;
  margin-right: 14vh;
}

p.footer-head {
  font-size: 1.9rem;
  margin-bottom: 20px;
  font-weight: 900;
}

.footer-links-all {
  display: flex;
  flex-direction: column;
}

.footer-links-all > a {
  margin-bottom: 8px;
  text-decoration: none;
  color: #120ef1;
}

.footer-links-all > a:hover {
  color: #2f20b8;
}

.footer-last {
  display: flex;
  flex-direction: column;
}

.footer-head-last {
  font-size: 1.8rem;
  font-weight: 900;
  margin-bottom: 20px;
}

.footer-subscribe {
  display: flex;
  width: 115%;
}

.footer-links-para {
  background: #979595;
  color: #fff;
  justify-content: flex-start;
  padding: 10px 40px 10px 10px;
}

.btn-footer-para {
  padding: 10px 20px;
  background-color: red;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
}

.btn-footer-paraclick {
  padding: 10px 20px;
  background-color: green;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
}

.footer-para-end {
  text-align: center;
}
