.touch {
  display: flex;
  flex-direction: column;
  background: #202020;
  color: #fff;
  height: 36vw;
}

.touch-full {
  display: flex;
  flex-direction: column;
  padding-top: 30px;
  padding-bottom: 40px;
}

.touch-head {
  font-size: 2.5rem;
  margin-top: 10px;
  margin-bottom: 5vh;
  text-align: center;
}

.touch-head::after {
  content: "";
  width: 32.5vh;
  height: 5px;
  display: block;
  background: #fff;
  margin: 10px auto 8px;
}

.touch-main {
  display: flex;
  width: 100%;
}

.touch-first-part {
  margin-left: 20vh;
  margin-right: 10vh;
}

.form-part {
  margin-bottom: 10px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.form-group {
  /* padding: 12px 55vh 12px 5px; */
  height: 6.5vh;
  width: 80vh;
  border-radius: 2px;
  border: none;
  outline: none;
  padding-left: 5px;
  padding-right: 10px;
}

.form-group::placeholder {
  font-size: 1rem;
}

.form-group-text {
  resize: none;
  width: 100%;
  height: 14vh;
  font-size: 1rem;
  padding-top: 5px;
  padding-left: 5px;
  border-radius: 2px;
  border: none;
  outline: none;
}

.btn-form {
  padding: 10px 15px;
  border-radius: 4px;
  border: none;
  outline: none;
  font-size: 1rem;
  color: black;
}

.btn-form:hover {
  background: #fff;
  cursor: pointer;
}

.touch-second-part {
  font-size: 1.1rem;
  margin-top: 6px;
}

.follow {
  margin-bottom: 10px;
  padding: 15px 25vh 12px 13px;
  color: #525050;
  background: #fff;
  border-radius: 2px;
}

b {
  margin-right: 10px;
  color: #332e2e;
  font-size: 1.2rem;
}

.touch-link {
  margin-right: 5px;
}

.touch-link > i {
  font-size: 1.2rem;
}

.fa-facebook {
  color: #3b5998;
}

.fa-youtube-play {
  color: #c4302b;
}

.fa-twitter {
  color: #00acee;
}

.fa-instagram {
  color: rgb(193, 53, 132);
}

.fa-google-plus {
  color: #db4a39;
}

.fa-facebook:hover,
.fa-google-plus:hover,
.fa-instagram:hover,
.fa-youtube-play:hover,
.fa-twitter:hover {
  transform: scale(1.2);
  transition: 200ms;
}
