.Navbar {
  background: linear-gradient(90deg, rgb(44, 44, 46) 0%, rgb(20, 20, 22) 100%);
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  left: 0;
}

.Navbar-logo {
  justify-self: start;
  color: #fff;
  margin-left: 20px;
  cursor: pointer;
}

.fa-typo3 {
  margin-left: 5px;
  font-size: 1.6rem;
}

.menu-item {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-links {
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
}

.nav-links:hover {
  color: #1d1d20;
  background: #fff;
  transition: all 0.3s ease-in;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .Navbar {
    position: relative;
  }

  .menu-item {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 500px;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .menu-item.active {
    background: #1b1b1b;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #2b2929;
    border-radius: 0;
    color: #fff;
  }

  .Navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(20%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    font-size: 1.7rem;
    top: 0;
    right: 0;
    transform: translate(-80%, 65%);
    cursor: pointer;
    color: #fff;
  }

  .nav-links-mobile {
    display: inline-block;
    text-decoration: none;
    color: #fff;
    text-align: center;
    margin: 0.6rem auto;
    border-radius: 4px;
    width: 80%;
    background: #3a3838;
    padding: 1.4rem 2rem;
    font-size: 1.6rem;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #3a3838;
    transition: 250ms;
  }

  Button {
    display: none;
  }
}
