.price {
  display: flex;
  flex-direction: column;
  background: #efefef;
  align-items: center;
  height: 57vw;
  /* padding: 40px; */
  /* font-size: 1.1rem; */
}

.price-full {
  display: flex;
  flex-direction: column;
  margin: 40px 0px;
}

.price-title {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 20px;
}

.price-title::after {
  content: "";
  background: #2e2c2c;
  display: block;
  height: 5px;
  width: 13vw;
  margin: 10px auto 5px;
}

.price-plans {
  display: flex;
  justify-content: flex-start;
  /* background: red; */
}

.price-details {
  /* background: yellow; */
  width: 19.5vw;
  display: inline;
  float: left;
  margin: 30px 15px;
  border: 1px solid #dfdddd;
}

.price-details:hover {
  box-shadow: 0 2px 20px #333;
}

.price-details-title {
  text-align: center;
  background: #2e2c2c;
  padding: 20px 0;
}

.price-details-title > h2 {
  font-size: 2rem;
  margin-bottom: 5px;
  color: #fff;
}

.details-para {
  color: #ccc;
  font-size: 30px;
}

.details-para span {
  font-size: 15px;
}

.price-details-list {
  list-style: none;
  display: inline;
  float: left;
  width: 100%;
}

.price-details-list li {
  border-bottom: 1px solid #efefef;
  padding: 22px 18px;
  background: #fff;
}

li:last-child {
  border: none;
}

i {
  margin-right: 10px;
  font-size: 1rem;
}

.fa-check-circle {
  color: green;
}

.fa-times-circle {
  color: red;
}

.btn-details {
  text-align: center;
  background: #fff;
}

.buy-btn {
  padding: 25px 65px;
  margin-top: 8px;
  margin-bottom: 30px;
  background-color: #242323;
  border-radius: 2px;
  border: none;
  outline: none;
  color: #fff;
}

.buy-btn:hover {
  color: #fff;
  background-color: #161616;
  cursor: pointer;
}
