.service {
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8));
  background-size: cover;
  background-position: center;
  color: #efefef;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  font-size: 1.2rem;
  height: 118vh;
}

.service-full {
  margin: 5vh;
}

.service-title {
  font-size: 2.5rem;
  margin-bottom: 15px;
}

.service-title-desc {
  margin-bottom: 4vh;
}

.service-text {
  width: 100%;
}

.service-text {
  display: flex;
  margin-bottom: 30px;
}

.service-text-desc {
  margin: 1vh;
  height: 35vh;
}

.service-text-desc:hover {
  background: #007bff;
  cursor: pointer;
  transition: 500ms;
}

.service-text-desc > i {
  font-size: 2.8rem;
  margin: 25px 0 10px 0;
  text-align: center;
}

.service-text-desc > h3 {
  font-weight: 600;
  font-size: 1.8rem;
  margin-bottom: 15px;
}

.service-text-desc > p {
  text-align: center;
  font-size: 1rem;
  margin: 0 20px 0;
}

.btn-service {
  padding: 8px 30px;
  border-radius: 2px;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 1rem;
}

.btn-service:hover {
  background-color: #fff;
}
