.About {
  display: flex;
  background-color: #181717;
  height: 40vw;
  font-size: 1.3rem;
  align-items: center;
}

.about-items {
  margin: 4rem;
}

.about-desc {
  margin-bottom: 2rem;
  color: #fff;
}

.about-desc > h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

.about-desc > p {
  text-align: justify;
}

.img-about {
  margin-right: 2rem;
}

.img-first {
  animation-name: anime;
  animation-duration: 3s;
}

.btn-mobile-first {
  display: none;
}

@keyframes anime {
  0% {
    opacity: 0;
    transform: translateY(-20%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

@media screen and (max-width: 960px) {
  .About {
    height: 73vw;
  }

  .about-desc > h1 {
    font-size: 1.5rem;
  }

  .about-desc > p {
    font-size: 0.8rem;
    margin-bottom: -10px;
  }

  .img-first {
    width: 40vw;
  }

  .btn-mobile-first {
    display: inline;
    padding: 8px 20px;
    margin-bottom: 20px;
    border-radius: 4px;
    border: none;
    outline: none;
    cursor: pointer;
  }

  .btn-mobile-first:hover {
    background-color: #fff;
    padding: 8px 20px;
    cursor: pointer;
  }
}
